import {ADD_FREE_DOMAIN} from "./fakeApiTypes";
import {caniAxios} from "cani-common-react";
const data = require('./../data/data.json');

export const addDomain  = (domain) => async(dispatch, getState) => {
  const savedDomain = await caniAxios.post("/integrations/api/v1/"+getState().client.clientCode+"/endpoints", domain);
  dispatch({
    type: ADD_FREE_DOMAIN,
    payload: {value:savedDomain.data.name}
  });
};
