import MapIcon from '@material-ui/icons/Map';
import {Hidden, List, ListItem} from "@material-ui/core";
import React from "react";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemText from "@material-ui/core/ListItemText";
import Menu from "@material-ui/core/Menu";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Divider from "@material-ui/core/Divider";
import Avatar from "@material-ui/core/Avatar";
import {Link as RouterLink} from "react-router-dom";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Link from "@material-ui/core/Link";
import {connect} from "react-redux";
import {get, hideModal, post, put, showModal} from "cani-common-react";
import {withRouter} from "react-router";
import {openLoginPopup, openRegisterPopup, LocalLoginPopup, RegisterPopup} from "cani-common-react";

const pages = [
    {
        title: "Usage",
        path: "usages",
        icon: MapIcon
    },
    {
        title: "FAQs",
        path: "/faqs",
        icon: MapIcon
    },
    {
        title: "Pricing",
        path: "/pricing",
        icon: MapIcon
    }
    ]
;

export default pages;

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        maxWidth: "300px"
    },
    horizMenu: { '& div' : {
            display: 'flex'
        }
    },
     flexContainer :{
        display: 'flex',
        flexDirection: 'row',
        padding: 0,
    }
}));

const options = [
    'Show some love to Material-UI',
    'Show all notification content',
    'Hide sensitive notification content',
    'Hide all notification content',
];

const TopMenuOne = (props) => {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [selectedIndex, setSelectedIndex] = React.useState(0);

    function handleClickListItem(event) {
        setAnchorEl(event.currentTarget);
    }

    function handleMenuItemClick(event, index) {
        setSelectedIndex(index);
        setAnchorEl(null);
        if(index === 2) {
          props.history.push("/domains");
        }
    }

    function handleClose() {
        setAnchorEl(null);
    }

    console.log(props.openLoginPopup);

    return (
        <div className={classes.root}>
            <LocalLoginPopup/>
            <RegisterPopup/>
            <List dense={true} component="nav" className={classes.flexContainer}  aria-label="Device settings">
                <Hidden mdDown>
                <ListItem dense={true}
                    button
                    aria-haspopup="true"
                    aria-controls="lock-menu"
                    aria-label="when device is locked"
                >
                    <ListItemText primary="Usage" />{/* secondary={options[selectedIndex]}*/}
                </ListItem>
                <ListItem  dense={true}
                    button
                    aria-haspopup="true"
                    aria-controls="lock-menu"
                    aria-label="when device is locked"
                >
                    <ListItemText primary="FAQs" />{/* secondary={options[selectedIndex]}*/}
                </ListItem>
                <ListItem  dense={true}
                    button
                    aria-haspopup="true"
                    aria-controls="lock-menu"
                    aria-label="when device is locked"
                >
                    <ListItemText primary="Pricing" />{/* secondary={options[selectedIndex]}*/}
                </ListItem>
                </Hidden>
                {!props.accessToken && <ListItem  dense={true}
                                                       button
                                                       aria-haspopup="true"
                                                       aria-controls="lock-menu"
                                                       aria-label="when device is locked"
                                                       onClick={props.openLoginPopup}
                >
                    <ListItemText primary="Login" /></ListItem>}
                {!props.accessToken && <ListItem  dense={true}
                                                       button
                                                       aria-haspopup="true"
                                                       aria-controls="lock-menu"
                                                       aria-label="when device is locked"
                                                       onClick={props.openRegisterPopup}
                >
                    <ListItemText primary="Register" /></ListItem>}
                {!props.accessToken && <ListItem dense={true}
                                                 button
                                                 aria-haspopup="true"
                                                 aria-controls="lock-menu"
                                                 aria-label="when device is locked"
                                                 onClick={handleClickListItem}
                >
                    <ListItemText primary="Sign In"/>{/* secondary={options[selectedIndex]}*/}
                </ListItem>
                }

                {props.accessToken && <div onClick={handleClickListItem}><ListItem dense={true}
                           button
                           aria-haspopup="true"
                           aria-controls="lock-menu"
                           aria-label="when device is locked"
                >
                    <Avatar
                        alt="Person"
                        className={classes.avatar}
                        component={RouterLink}
                    >
                        {props.name.substring(0, 1)}
                    </Avatar>
                    <Link href="#" variant="body2">
                        <ListItemText secondary={props.name}/>{/* secondary={options[selectedIndex]}*/}
                    </Link>
                    <ExpandMoreIcon fontSize="small"/>
                </ListItem>
                </div>
                }
            </List>
            <Menu
                id="lock-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                    <MenuItem
                        key={'edit account'}
                        selected={1 === selectedIndex}
                        onClick={event => handleMenuItemClick(event, 1)}
                    >
                        Edit Account
                    </MenuItem>
                <Divider/>
                <MenuItem
                    key={'edit account'}
                    selected={2 === selectedIndex}
                    onClick={event => handleMenuItemClick(event, 2)}
                >
                    Your Endpoints
                </MenuItem>
                <MenuItem
                    key={'subscription'}
                    selected={3 === selectedIndex}
                    onClick={event => handleMenuItemClick(event, 3)}
                >
                    Your Subscription
                </MenuItem>
            </Menu>
        </div>
    );
};

const mapStateToProps = (state, ownProps) => {
    return {
        name: state.auth.userInfo ? state.auth.userInfo.name : "",
        userId: state.auth.userInfo ? state.auth.userInfo.id : "",
        accessToken: state.auth.tokenInfo.accessToken,
    }
};


export const TopMenu =  withRouter(connect(mapStateToProps, {
    post, get, put, showModal,
    hideModal,openLoginPopup, openRegisterPopup
})(TopMenuOne));
