import React, {useEffect, useState} from 'react';
import './DomainView.css';
import {connect} from "react-redux";
import {Card, Divider, Avatar, DialogTitle, IconButton, DialogContent, Fab} from "@material-ui/core";
import {post, get, showModal, hideModal, put, clearMessage} from 'cani-common-react'
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import Highlight from 'react-highlight'
import SettingsApplicationsIcon from '@material-ui/icons/SettingsApplications';
import Link from "@material-ui/core/Link";
import Dialog from "@material-ui/core/Dialog";
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import CardHeader from "@material-ui/core/CardHeader";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import NativeSelect from "@material-ui/core/NativeSelect";
import TextField from "@material-ui/core/TextField";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import {Field, reduxForm} from "redux-form";
import {renderSelect, renderTextField} from "../../components/RenderComponents";
import MenuItem from "@material-ui/core/MenuItem";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableFooter from "@material-ui/core/TableFooter";
import TablePagination from "@material-ui/core/TablePagination";
import TablePaginationActions from "@material-ui/core/TablePagination/TablePaginationActions";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import Collapse from "@material-ui/core/Collapse";
import Typography from "@material-ui/core/Typography";

const DomainView = (props) => {

    const apiListSize = props.apiList ? props.apiList.length : 0;
    const [id, setId] = useState('');
    const [ruleId, setRuleId] = useState('');
    let currentRule = {responseStatus: 200, field: 'BODY', operator: "CONTAINS"};
    const [requestMethod, setRequestMethod] = useState("GET");
    const [url, setUrl] = useState("");
    const [apiId, setApiId] = useState("");
    const [pathType, setPathType] = useState("EXACT_MATCH");
    const [responseStatus, setResponseStatus] = useState(200);
    const [responseDelayInSec, setResponseDelayInSec] = useState(0.0);
    const [header, setHeader] = useState("{}");
    const [response, setResponse] = useState("{}");
    const [selectedRow, setSelectedRow] = useState(-1);
    console.log(selectedRow);
    useEffect(() => {
        props.post(false, 'integrations9', 'integrations', 'endpoints', 'search', {
            criterias: [{
                field: "name",
                caseInsensitive: false,
                value: props.match.params.name
            }]
        }, 'endpoint-get')
    }, []);

    useEffect(() => {
        if(props.domain) {
            setId(props.domain.id);
        }
    }, [props.domain]);

    useEffect(() => {
        if (apiId) {
            props.get(false, 'integrations9', 'integrations', 'faker-apis', '' + apiId, 'faker-get-id', 'faker-get-id');
            props.post(false, 'integrations9', 'integrations', 'api-rules', 'search', {
                 criterias: [{
                    fieldType: "LONG",
                    field: "fakerApiId",
                    value: apiId
                }]
            }, 'api-rules-list');
        }
    }, [apiId]);

    useEffect(() => {
        if (props.currentApi) {
            setApiId(props.currentApi.id);
            setRequestMethod(props.currentApi.requestMethod);
            setPathType(props.currentApi.pathType);
            setUrl(props.currentApi.url);
            setResponseStatus(props.currentApi.responseStatus);
            setResponseDelayInSec(props.currentApi.responseStatusInSec);
            setHeader(props.currentApi.header);
            setResponse(props.currentApi.response);
        }
    }, [props.currentApi]);


    useEffect(() => {
        props.post(false, 'integrations9', 'integrations', 'faker-apis', 'search', {
            criterias: [{
                field: "endpoint.name",
                caseInsensitive: false,
                value: props.match.params.name
            }]
        }, 'api-list-get')
    }, []);

    useEffect(() => {
        if (ruleId) {
            props.get(false, 'integrations9', 'integrations', 'api-rules', '' + ruleId, 'rule-id', 'rule-get-id');
        }
    }, [ruleId]);

    useEffect(() => {
        if (props.updateRuleStatus && props.updateRuleStatus.success) {
            props.clearMessage("integrations", "api-rules", [""]);
            closeRuleEditPopup();
            setRuleId(null);
        }
    }, [props.updateRuleStatus
    ]);
    useEffect(() => {
        props.post(false, 'integrations9', 'integrations', 'faker-api-run-histories', 'search', {
            limit: 10, pageNo:1,  criterias: [{
                field: "endpoint.name",
                caseInsensitive: false,
                value: props.match.params.name
            }]
        }, 'api-run-histories-list')
    }, []);
    const openApiPopup = () => {
        props.showModal("integration", 'mock-api-code', 'list');
    };
    const closeApiPopup = () => {
        setApiId(null);
        props.hideModal("integration", 'mock-api-code', 'list');
    };
    const openApiEditPopup = () => {
        props.showModal("integration", 'mock-api-code', 'edit');
    };
    const closeApiEditPopup = () => {
        props.hideModal("integration", 'mock-api-code', 'edit');
        setApiId(null);
    };
    const openRuleEditPopup = () => {
        props.showModal("integration", 'mock-api-rule', 'edit');
    };
    const closeRuleEditPopup = () => {
        props.hideModal("integration", 'mock-api-rule', 'edit');
        setRuleId(null);
    };
    const edit = (id) => {
        setApiId(id);
        openApiEditPopup();
    };

    const create = () => {
        setApiId(null);
        openApiEditPopup();
    };

    const editRule = (id) => {
        setRuleId(id);
        openRuleEditPopup();
    };

    const createRule = () => {
        currentRule = {responseStatus: 200, field: 'BODY', operator: "CONTAINS"};
        setRuleId(null);
        openRuleEditPopup();
    };

    const deleteRule = (id) => {
        props.delete(false, 'integrations9', 'integrations', 'api-rules', '' + id, 'rule-id', 'rule-get-id');
    };

    const deleteApi = (id) => {
        props.delete(false, 'integrations9', 'integrations', 'faker-apis', '' + id, 'rule-id', 'rule-get-id');
    };

    const updateApi = () => {
        props.put(false, 'integrations9', 'integrations', 'faker-apis', '', {
            id: apiId,
            requestMethod,
            response,
            header,
            pathType,
            url,
            responseDelayInSec,
            responseStatus
        }, "update");
    };

    const saveApi = () => {
        props.post(false, 'integrations9', 'integrations', 'faker-apis', '', {
            endpointId: id,
            requestMethod,
            response,
            header,
            pathType,
            url,
            responseDelayInSec,
            responseStatus
        }, "update");

    };

    const updateRule = async (values) => {
        console.log(values);
        console.log(ruleId);
        if (ruleId) {
            props.put(false, 'integrations9', 'integrations', 'api-rules', '', {
                id: ruleId,
                fakerApiId: apiId,
                ...values
            }, "update");
        } else {
            props.post(false, 'integrations9', 'integrations', 'api-rules', '', {fakerApiId: id, ...values}, "udpate");
        }
    };

    return (
        <div className="App">
            <Card>
                <>
                    <Grid container={true}><Grid md={6} item={true}><Grid container><Grid item justify={"flex-end"} xs={6}><Grid container justify={'flex-end'}><Typography variant={'h2'} >{props.match.params.name}</Typography></Grid></Grid><Grid item xs={6}><Grid direction={'row'} container alignItems={"flex-end"} justify={'flex-start'}> {'.api.monk.com'}</Grid></Grid></Grid></Grid>
                        <Grid md={6} item={true}> <Grid justify={'flex-end'} alignItems={"flex-end"} container={true}>
                            <Link onClick={openApiPopup}><SettingsApplicationsIcon/> Mock Apis({apiListSize}) </Link>
                        </Grid></Grid></Grid>
                </>
                <Divider/>
                <Table aria-label="custom pagination table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Method Type</TableCell>
                            <TableCell>URL</TableCell>
                            <TableCell>Response</TableCell>
                            <TableCell>Matching Api</TableCell>
                            <TableCell>Matching Rule</TableCell>
                            <TableCell>Action</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {props.apiRunHistoryList && props.apiRunHistoryList.map((row, index) => {
                            return <><TableRow style={{cursor: 'pointer'}} hover={true}
                                               onClick={(event) => setSelectedRow(index)}
                                               selected={index === selectedRow} key={row.name}>
                                <TableCell component="th" scope="row">
                                    {row.requestMethod}
                                </TableCell>
                                <TableCell>
                                    <div style={{
                                        display: 'inline-block',
                                        paddingLeft: '3px',
                                        paddingRight: '3px',
                                        borderRadius: 4,
                                        color: '#c7254e',
                                        backgroundColor: '#f9f2f4',
                                        width: 'auto'
                                    }}>
                                        <>{row.url}</>
                                    </div>
                                </TableCell>
                                <TableCell><Avatar style={{
                                    borderRadius: 5,
                                    height: 'auto',
                                    width: 'auto',
                                    display: "inline-block",
                                    padding: '0px 2px',
                                    backgroundColor: row.responseStatus >= 200 && row.responseStatus <= 299 ? '#18bc9c' : 'red'
                                }}>{row.responseStatus}</Avatar></TableCell>
                                <TableCell>{row.fakerApiId && <CheckCircleIcon/>}</TableCell>
                                <TableCell>{row.apiRuleId && <CheckCircleIcon/>}</TableCell>
                                <TableCell>{row.apiRuleId && <CheckCircleIcon/>}</TableCell>
                                <TableCell>{row.apiRuleId && <CheckCircleIcon/>}</TableCell>

                            </TableRow>
                                {index === selectedRow && <TableRow selected={true}>
                                    <TableCell colSpan={7}>
                                        <Grid spacing={2} container={true}>
                                            <Grid item md={6}>
                                                <Grid container>
                                                    <Grid xs={6}><Typography>Request Body</Typography></Grid>
                                                    <Grid xs={6}><Grid container justify={"flex-end"}><Link>View Headers</Link>
                                                    </Grid>
                                                    </Grid>
                                                </Grid>
                                                <TextField fullWidth={true} variant={'outlined'} rows={8}
                                                           multiline={true}
                                                           value={row.request} disabled={true}/>
                                            </Grid>
                                            <Grid item md={6}>
                                                <Grid container>
                                                    <Grid xs={6}><Typography>Response Body</Typography></Grid>
                                                    <Grid xs={6}><Grid container justify={"flex-end"}><Link>View Headers</Link>
                                                    </Grid>
                                                    </Grid>
                                                </Grid>
                                                <TextField fullWidth={true} variant={'outlined'} rows={8}
                                                           multiline={true}
                                                           value={row.response} disabled={true}/>
                                            </Grid>
                                        </Grid>
                                    </TableCell>
                                </TableRow>}</>
                        })}
                    </TableBody>
                    < TableFooter>
                        < TableRow>
                            {/*<TablePagination
                                rowsPerPageOptions={[5, 10, 25]}
                                colSpan={3}
                                count={rows.length}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                SelectProps={{
                                    inputProps: { 'aria-label': 'rows per page' },
                                    native: true,
                                }}
                                onChangePage={handleChangePage}
                                onChangeRowsPerPage={handleChangeRowsPerPage}
                                ActionsComponent={TablePaginationActions}
                            />*/
                            }
                        </TableRow>
                    </TableFooter>
                </Table>
                {props.apiRunHistoryList && props.apiRunHistoryList.map(domain => {
                    return <Card style={{marginTop: '5px'}}> <CardContent> <Grid container={true}><Grid item
                                                                                                        md={4}>{domain.requestMethod + ' '}
                        <div style={{
                            display: 'inline-block',
                            paddingLeft: '3px',
                            paddingRight: '3px',
                            borderRadius: 4,
                            color: '#c7254e',
                            backgroundColor: '#f9f2f4',
                            width: 'auto'
                        }}>
                            <>{domain.url}</>
                        </div>
                    </Grid> <Grid item md={4}><Avatar style={{
                        borderRadius: 5,
                        height: 'auto',
                        width: 'auto',
                        display: "inline-block",
                        padding: '0px 2px',
                        backgroundColor: domain.responseStatus >= 200 && domain.responseStatus <= 299 ? '#18bc9c' : 'red'
                    }}>{domain.responseStatus}</Avatar></Grid>
                        <Grid item md={4}>{}</Grid></Grid></CardContent></Card>
                })}
            </Card>
            <Dialog
                fullWidth={true}
                disableBackdropClick
                disableEscapeKeyDown
                maxWidth="lg"
                aria-labelledby="confirmation-dialog-title"
                open={props.apiModalShown}
            >
                <DialogTitle id="confirmation-dialog-title">{'Mock Api'}
                    <IconButton aria-label="close" onClick={closeApiPopup}>
                        <CloseIcon/>
                    </IconButton>
                    <IconButton onClick={create} aria-label="close">
                        <AddIcon/> Add Api
                    </IconButton>
                </DialogTitle>
                <DialogContent dividers>
                    {props.apiList && props.apiList.map(domain => {
                        // @ts-ignore
                        return <Card style={{marginTop: '5px'}}> <CardContent> <Grid container={true}><Grid item
                                                                                                            md={4}>{domain.requestMethod}-
                            <div style={{
                                display: 'inline-block',
                                paddingLeft: '3px',
                                paddingRight: '3px',
                                borderRadius: 4,
                                color: '#c7254e',
                                backgroundColor: '#f9f2f4',
                                width: 'auto'
                            }}>
                                <>{domain.url}</>
                            </div></Grid> <Grid item md={4}><Avatar style={{
                            borderRadius: 5,
                            backgroundColor: domain.responseStatus >= 200 && domain.responseStatus <= 299 ? '#18bc9c' : 'red'
                        }}>{domain.responseStatus}</Avatar></Grid><Grid item md={4}> <Fab color='primary'
                                                                                          size={"small"}>
                            <EditIcon format="primary" size="xs" onClick={() => edit(domain.id)}>Edit
                            </EditIcon>
                        </Fab>

                            <Fab color='primary'
                                 size={"small"}>
                                <DeleteIcon format="primary" size="xs" onClick={() => deleteApi(domain.id)}>Edit
                                </DeleteIcon>
                            </Fab>
                        </Grid></Grid></CardContent></Card>
                    })}
                </DialogContent>
                <Dialog
                    fullWidth={true}
                    disableBackdropClick
                    disableEscapeKeyDown
                    maxWidth="lg"
                    aria-labelledby="confirmation-dialog-title"
                    open={props.apiEditModalShown}
                >
                    <DialogTitle id="confirmation-dialog-title">{'Mock Api'}
                        <IconButton aria-label="close" onClick={closeApiEditPopup}>
                            <CloseIcon/>
                        </IconButton>

                    </DialogTitle>
                    <DialogContent dividers>
                        <Grid container spacing={3}>
                            <Grid item xs={12}>
                                <Card>
                                    <CardHeader title={'Define the Route of the app '}>
                                    </CardHeader>
                                    <Divider/>
                                    <CardContent>
                                        <Grid container spacing={3}>
                                            <Grid item md={3}>
                                                <FormControl fullWidth={true} variant={'outlined'}>
                                                    <InputLabel htmlFor="age-native-helper">Request Method</InputLabel>
                                                    <NativeSelect
                                                        value={requestMethod}
                                                        onChange={(event) => setRequestMethod(event.target.value)}
                                                        inputProps={{
                                                            name: 'age',
                                                            id: 'age-native-helper',
                                                        }}
                                                    >
                                                        <option value="GET">GET</option>
                                                        <option value="POST">POST</option>
                                                        <option value="PUT">PUT</option>
                                                        <option value="GET">PATCH</option>
                                                        <option value="POST">DELETE</option>
                                                        <option value="OPTIONS">OPTIONS</option>
                                                        <option value="TRACE">TRACE</option>
                                                    </NativeSelect>
                                                </FormControl>
                                            </Grid>
                                            <Grid item md={3}>
                                                <FormControl fullWidth={true}>
                                                    <InputLabel htmlFor="age-native-helper">Request
                                                        Condition</InputLabel>
                                                    <NativeSelect
                                                        value={pathType}
                                                        onChange={(event) => setPathType(event.target.value)}
                                                        inputProps={{
                                                            name: 'age',
                                                            id: 'age-native-helper',
                                                        }}
                                                    >
                                                        <option value="EXACT_MATCH">Exact Match</option>
                                                        <option value="CONTAINS">Contains</option>
                                                        <option value="PATTERN_MATCH">Pattern Match</option>
                                                        <option value="REGEX_MATCH">Regex Match</option>
                                                    </NativeSelect>
                                                </FormControl>
                                            </Grid>
                                            <Grid item md={6}>
                                                <TextField label={'URL'} variant={'outlined'} fullWidth={true}
                                                           value={url}
                                                           onChange={(event) => setUrl(event.target.value)}></TextField>
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12}>
                                <Card>
                                    <CardHeader title={'Rules If any If you don\'t want default response'}
                                                action={<IconButton onClick={createRule} aria-label="close">
                                                    <AddIcon/> Add Rule
                                                </IconButton>}>

                                    </CardHeader>
                                    <Divider/>
                                    <CardContent>
                                        {props.apiRuleList && props.apiRuleList.map(domain => {
                                            // @ts-ignore
                                            return <Card style={{marginTop: '5px'}}> <CardContent> <Grid
                                                container={true}><Grid item md={1}><Highlight>When</Highlight>
                                            </Grid> <Grid item md={1}><Highlight
                                                laungauge={'javascript'}>{domain.field}</Highlight></Grid> <Grid
                                                item md={1}><Highlight
                                                laungauge={'javascript'}>{domain.operator}</Highlight></Grid>
                                                <Grid item md={2}><Highlight
                                                    laungauge={'javascript'}>{domain.value}</Highlight></Grid>
                                                <Grid item md={1}>
                                                    <Highlight laungauge={'javascript'}>return</Highlight>
                                                </Grid>
                                                <Grid item md={1}>
                                                    <Highlight
                                                        laungauge={'javascript'}> {domain.responseStatus}</Highlight>
                                                </Grid>
                                                <Grid item md={5}>
                                                    <Fab color='primary' size={"small"}>
                                                        <EditIcon format="primary" size="xs"
                                                                  onClick={() => editRule(domain.id)}>Edit
                                                        </EditIcon>
                                                    </Fab>
                                                    <Fab color='primary'
                                                         size={"small"}>
                                                        <DeleteIcon format="primary" size="xs"
                                                                    onClick={() => deleteRule(domain.id)}>Delete
                                                        </DeleteIcon>
                                                    </Fab>
                                                </Grid></Grid></CardContent></Card>
                                        })}
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12}>
                                <Card>
                                    <CardHeader title={'Default Response'}>
                                    </CardHeader>
                                    <Divider/>

                                    <CardContent>
                                        <Grid spacing={3} container={true}>
                                            <Grid item sm={4}>
                                                <TextField InputLabelProps={{shrink: true}} type={'number'}
                                                           label={'Response Delayed By'} variant={'outlined'}
                                                           fullWidth={true} value={responseDelayInSec}
                                                           onChange={(event) => setResponseDelayInSec(event.target.value)}/>
                                            </Grid>
                                            <Grid item sm={4}>
                                                <TextField type={'number'} label={'Return Http Response As'}
                                                           variant={'outlined'} fullWidth={true} value={responseStatus}
                                                           onChange={(event) => setResponseStatus(event.target.value)}/>
                                            </Grid>
                                            <Grid item sm={4}>
                                            </Grid>
                                            <Grid item sm={6}>
                                                <TextField rows={8} label={'Default Response'} multiline={true}
                                                           variant={'outlined'} fullWidth={true} value={response}
                                                           onChange={(event) => setResponse(event.target.value)}/>
                                            </Grid>
                                            <Grid item sm={6}>
                                                <TextField rows={8} label={'Default Header'} multiline={true}
                                                           variant={'outlined'} fullWidth={true} value={header}
                                                           onChange={(event) => setHeader(event.target.value)}/>
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                    <CardActions>
                                        {!apiId && <Button variant={"outlined"} color={'primary'}
                                                        onClick={saveApi}> Save </Button>}
                                        {apiId && <Button variant={"outlined"} color={'primary'}
                                                       onClick={updateApi}> Update </Button>}
                                    </CardActions>
                                </Card>
                            </Grid>
                        </Grid>
                        <Dialog
                            fullWidth={true}
                            disableBackdropClick
                            disableEscapeKeyDown
                            maxWidth="lg"
                            aria-labelledby="confirmation-dialog-title"
                            open={props.ruleEditModalShown}
                        >
                            <DialogTitle id="confirmation-dialog-title">{'Api Rule'}
                                <IconButton aria-label="close" onClick={closeRuleEditPopup}>
                                    <CloseIcon/>
                                </IconButton>

                            </DialogTitle>
                            <DialogContent dividers>
                                <ApiRuleForm initialValues={{...currentRule, ...props.currentRule}}
                                             onSubmit={updateRule}/>
                            </DialogContent>
                        </Dialog>
                    </DialogContent>
                </Dialog>
            </Dialog>
        </div>
    );
};

const mapStateToProps = (state, ownProps) => {
    return {
        domain: state.common['integrations_endpoints_endpoint-get'] && state.common['integrations_endpoints_endpoint-get'][0],
        apiList: state.common['integrations_faker-apis_api-list-get'],
        apiRuleList: state.common['integrations_api-rules_api-rules-list'],
        apiRunHistoryList: state.common['integrations_faker-api-run-histories_api-run-histories-list'],
        apiModalShown: state.common.modalStatus['integration_mock-api-code_list'],
        apiEditModalShown: state.common.modalStatus['integration_mock-api-code_edit'],
        ruleEditModalShown: state.common.modalStatus['integration_mock-api-rule_edit'],
        updateRuleStatus: state.status['integrations_api-rules_update'],
        currentApi: state.common['integrations_faker-apis_faker-get-id'],
        currentRule: state.common['integrations_api-rules_rule-get-id']
    }
};


export default connect(mapStateToProps, {
    post, get, put, showModal,
    hideModal, clearMessage
})(DomainView);

const ApiRuleEdit = (props) => {
    return <form onSubmit={props.handleSubmit}><Grid container spacing={3}>
        <Grid item xs={12}>
            <Card>
                <CardHeader title={'Define the Rule for this Route'}>
                </CardHeader>
                <Divider/>
                <CardContent>
                    <Grid container spacing={3}>
                        <Grid item md={3}>
                            <Field
                                variant={'outlined'}
                                name="field"
                                component={renderSelect}
                                label="Field"
                            >
                                <MenuItem value="BODY">BODY</MenuItem>
                                <MenuItem value="HEADER">HEADER</MenuItem>
                                <MenuItem value="REQUEST_PARAM">REQUEST PARAM</MenuItem>
                                <MenuItem value="PATH_VARIABLE">PATH VARIABLE</MenuItem>
                            </Field>
                        </Grid>
                        <Grid item md={3}>
                            <Field
                                variant={'outlined'}
                                name="operator"
                                component={renderSelect}
                                label="Match Condition"
                            >
                                <MenuItem value="CONTAINS">CONTAINS</MenuItem>
                                <MenuItem value="REGEX_MATCH">REGEX MATCH</MenuItem>
                            </Field>
                        </Grid>
                        <Grid item md={6}>
                            <Field
                                name="value"
                                component={renderTextField}
                                label="Value"
                                variant={'outlined'}
                            />
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </Grid>
        <Grid item xs={12}>
            <Card>
                <CardHeader title={'Default Response'}>
                </CardHeader>
                <Divider/>

                <CardContent>
                    <Grid spacing={3} container={true}>
                        <Grid item sm={12}>
                            <Field
                                type={'number'}
                                name="responseStatus"
                                variant={'outlined'}
                                component={renderTextField}
                                label="Return Http Response As"
                            />
                        </Grid>
                        <Grid item sm={6}>
                            <Field
                                name="response"
                                variant={'outlined'}
                                component={renderTextField}
                                label="Default Response"
                                multiline={true}
                                rows={8}
                            />
                        </Grid>
                        <Grid item sm={6}>
                            <Field
                                variant={'outlined'}
                                name="header"
                                component={renderTextField}
                                label="Default Header"
                                multiline={true}
                                rows={8}
                            />
                        </Grid>
                    </Grid>
                </CardContent>
                <CardActions>
                    <Button variant={"outlined"} color={'primary'} type={'submit'}> Update </Button>
                </CardActions>
            </Card>
        </Grid>
    </Grid>
    </form>
}

const validate = (values) => {
    const errors = {};
    const requiredFields = [
        'value',
        'responseStatus',
        'field',
        'operator'
    ];
    requiredFields.forEach(field => {
        if (!values[field]) {
            errors[field] = 'Required';
        }
    });
    /*if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
    errors.email = 'Invalid email address';
    }*/
    return errors;
}


const ApiRuleForm = reduxForm({
    form: 'apiRule',
    validate,
    enableReinitialize: true,
})(ApiRuleEdit);

