import { combineReducers } from 'redux';
import {clientReducers} from "cani-common-react";
import {authReducers} from 'cani-common-react';
import {statusReducers} from "cani-common-react"
import {commonReducers} from "cani-common-react";
import {eavReducers} from 'cani-common-react';
import {entityReducers} from 'cani-common-react';
import {usersReducers} from "cani-common-react";
import fakeApiReducers from "./fakeApiReducers";
import { reducer as formReducer } from 'redux-form';

export default combineReducers ({
  form: formReducer,
  client: clientReducers,
  auth: authReducers,
  status:statusReducers,
  common:commonReducers,
  eav: eavReducers,
  eavEntity: entityReducers,
  users: usersReducers,
  fakeApi:fakeApiReducers,
})
