import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import React from 'react'
import RadioGroup from "@material-ui/core/RadioGroup";
import Select from "@material-ui/core/Select";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";

export const renderTextField = (
    { color, variant, rows, multiline, input, label, type,  meta: { touched, error }, ...custom },
) => (
    <TextField
        type={type}
        multiline={multiline}
        rows={rows}
        label={label}
        fullWidth={true}
        variant={variant}
        color={color}
        error={touched && error}
        helperText={touched && error}
        {...input}
        {...custom}
    />
);

export const renderCheckbox = ({ color, input, label }) => (
    <Checkbox
        label={label}
        checked={!!input.value}
        onCheck={input.onChange}
        color={color}
    />
);

export const renderRadioGroup = ({ input, ...rest }) => (
    <RadioGroup
        {...input}
        {...rest}
        valueSelected={input.value}
        onChange={(event, value) => input.onChange(value)}
    />
);

export const renderSelect = (
    { input, label,  variant,  meta: { touched, error }, children, ...custom }
) => (
    <>
        {<TextField
        select
        fullWidth={true}
        label={label}
        variant={variant}
        error={touched && error}
        helperText={touched && error}
        {...custom}
        {...input}
        value={input.value}
    >
        {children}
    </TextField>}
        {/*<FormControl fullWidth={true} variant = {variant}>
        <InputLabel  htmlFor="outlined-age-simple">
            {label}
        </InputLabel>
        <Select
            onChange={(event, index, value) => input.onChange(value)}
            {...custom}
            inputProps={{
                name: 'age',
                id: 'outlined-age-simple',
            }}
            {...input}
        >
            {children}
        </Select>
     </FormControl>*/}
        </>
);
