import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {clearMessage, post} from "cani-common-react";
import {TextField, Grid, CssBaseline} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import makeStyles from "@material-ui/core/styles/makeStyles";
import List from "@material-ui/core/List";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import SendIcon from '@material-ui/icons/Send';
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import {addDomain} from "../../redux/fakeApiAction";

const useStyles = makeStyles(theme => ({
    createTextField: {
        float: 'left',
        display: 'flex',
        flexDirection: 'row',
        '& fieldset': {
            borderBottomRightRadius: '0px',
            borderTopRightRadius: '0px',
            borderRadius: '6px'
        }
    },
    addOn: {
        padding: '20px 15px',
        fontSize: '15px',
        fontWeight: 'normal',
        lineHeight: 1,
        color: '#2c3e50',
        textAlign: 'center',
        backgroundColor: '#ecf0f1',
        border: '1px solid #dce4ec',
        borderBottomLeftRadius: '0px',
        borderTopLeftRadius: '0px',
        borderRadius: '6px'
    }
}));


const Homepage = (props) => {
    const classes = useStyles();
    const [domain, setDomain] = useState("");
    const createEndpoint = () => {
        props.addDomain({"name": domain});
        //props.post(false, 'integrations9', 'integrations', 'endpoints', '', {name: domain}, "update");
    };
    useEffect(() => {
     //props.clearMessage("integrations", "endpoints", ["update"]);
     if(props.updateEndpointStatus && props.updateEndpointStatus.success) {

     }
    },[props.updateEndpointStatus]);
    return (
        <>
            <Container component="main" maxWidth="lg">
                <Paper elevation={1} square={true}>
                    <Box m={2}>
                        <CssBaseline/>
                        <Grid container={true}>
                            <Grid alignContent={'center'} xs={12}>
                                <Typography align={"center"} variant={'h5'}>
                                    Rest API mocking and intercepting in seconds.
                                    Replace the endpoint in the code and you are ready. It's that simple!
                                </Typography>
                                <Divider/>
                            </Grid>

                            <Grid xs={12} item={true}>
                                <Box m={2}>
                                <Grid container justify = "center">
                                    <div className={classes.createTextField}>
                                        <TextField
                                            variant={'outlined'}
                                            onChange={(event) => {setDomain(event.target.value)}}
                                            value={domain}
                                            id="adornment-weight"
                                            aria-describedby="weight-helper-text"
                                            inputProps={{
                                                'aria-label': 'weight',
                                                'height': '100px'
                                            }}
                                            label={'Domain'}
                                        />
                                        <div className={classes.addOn}> <Grid style={{alignSelf: 'center'}} container justify={"flex-end"} alignItems={"flex-end"}>com.canigenus.com</Grid></div>
                                    </div>
                                </Grid>
                                </Box>
                            </Grid>
                            <Grid alignContent={'center'} justify={"center"} alignItems={'center'} xs={12} item={true}>
                                <Typography align={"center"}>
                                A sub-domain will be created for this endpoint where you can send requests.
                                </Typography>
                            </Grid>
                            <Grid xs={12} item={true}>
                                <Grid container justify = "center">
                                <Button onClick={createEndpoint} color={'secondary'} variant={'contained'}>Create Endpoint</Button>
                                </Grid>
                            </Grid>
                        </Grid>

                        <List dense={true}
                              component="nav"
                              aria-labelledby="nested-list-subheader"
                              subheader={
                                  <Grid container justify = "center">
                                  <Typography  variant={'h6'} component="div" id="nested-list-subheader">
                                      Use Cases
                                  </Typography>
                                  </Grid>
                              }
                        >
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Build a mock Rest API in a few seconds. Free, No coding required."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Inspect payloads of any HTTP request (GET, POST, PUT, PATCH, DELETE, etc)."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="Customizable responses to simulate API response and failures."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="When load testing your API, do you really need to pass on the load to downstream APIs?"/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Simulate latencies, timeouts and slow-responses of downstream APIs (validate rarely reachable code paths)"/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="A/B testing by switching API endpoints or versions without any redeployment. (prod code vs new code)"/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Don't block your UI devs when backend APIs are still in development. Just mock it!"/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="Create webhook endpoints and simulate responses."/>
                            </ListItem>
                        </List>

                        <Divider/>
                        <List dense={true}
                              component="nav"
                              aria-labelledby="nested-list-subheader"
                              subheader={
                                  <Grid container justify = "center">
                                      <Typography  variant={'h6'} component="div" id="nested-list-subheader">
                                          Features
                                      </Typography>
                                  </Grid>
                              }
                        >
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="HTTP request intercepting for debugging and inspecting payload."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Get named endpoints/sub-domains - easy to replace base URL in your code."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Proxy Setup: Rule based approach to mock a few calls and hit real API for the rest."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="Capturing HTTP requests in real time."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText
                                    primary="Support for CORS & preflight requests (OPTIONS) is out of the box."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="Keep Calm & Get 200 OK."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="All endpoints are HTTPS enabled."/>
                            </ListItem>
                            <ListItem dense={true}>
                                <ListItemIcon>
                                    <SendIcon/>
                                </ListItemIcon>
                                <ListItemText primary="Requestbin + Mocky.io + Http-proxy => DummyMonk. "/>
                            </ListItem>
                        </List>
                    </Box>
                </Paper>
            </Container>
        </>
    );
}

function mapStateToProps(state) {
    return {
        accessToken: state.auth.tokenInfo.accessToken,
        clientCode: state.client.clientCode,
        permissions: state.auth.allPermissions,
        name: state.auth.userInfo ? state.auth.userInfo.name : "",
        updateEndpointStatus: state.status['integrations_endpoints_update'],
    };
}

export default connect(
    mapStateToProps,
    {post, clearMessage, addDomain}
)(Homepage);
