import {FREE_DOMAINS_LIST, ADD_FREE_DOMAIN, PAID_DOMAINS_LIST} from "./fakeApiTypes";

export default (state = {
  compareIds : [],
  freeDomains: []
}, action) => {
  switch (action.type) {
    case FREE_DOMAINS_LIST:
      return {...state , [action.payload.key]:  action.payload.values};
    case ADD_FREE_DOMAIN:
      return {...state , freeDomains: [ ...state.freeDomains, action.payload.value]};
    case PAID_DOMAINS_LIST:
      return {...state,"compareIds":[...state.compareIds,action.payload.value] };
    default:
      return state;
  }
};
