import React from 'react';
import {hot} from 'react-hot-loader'
import {BrowserRouter, Route, Switch} from "react-router-dom";
import Homepage from './admin/Homepage';
import {LoggedInLayout} from "cani-common-react";
import pages, {TopMenu} from 'menu';
import { ThemeProvider as  ThemeProviderMat} from '@material-ui/styles';
import theme from './theme'
import DomainList from 'pages/domain-list/DomainList';
import DomainView from "./pages/domain-view/DomainView";

const Router: React.FC = () => {
  return (
      <ThemeProviderMat theme={theme}>
          <BrowserRouter>
                <Switch>
                  <LoggedInLayout sidebarVariant={'temporary'} isOpen={true} sidebarMenu={pages} topBarMenu={<TopMenu/>}>
                    <Route exact path="/" component={Homepage} />
                    <Route exact path="/domains" component={DomainList} />
                    <Route exact path="/domains/:name" component={DomainView} />
                  </LoggedInLayout>
                </Switch>
          </BrowserRouter>
      </ThemeProviderMat>
  );
}

export default hot(module)(Router);
