import React, {useEffect} from 'react';
import './DomainList.css';
import {connect} from "react-redux";
import {Grid, Card, Divider, Link} from "@material-ui/core";
import {post} from 'cani-common-react'
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";

const DomainList: React.FC = (props: any) => {
    useEffect(() => {
        props.post(false,'integrations9', 'integrations',  'endpoints',  'search', {}, 'integrations-list')
    }, []);
  return (
    <div className="App">
        <Card>
            <CardHeader title={'Your Domains'}/>
            <Divider/>
        {props.domainList && props.domainList.map(domain => {
            return <Card style={{marginTop: '5px'}}> <Link href={"/domains/"+domain.name}><CardContent title={domain.name}> #{domain.name}</CardContent></Link></Card>
        })}
        </Card>
    </div>
  );
}

const mapStateToProps = (state) => {
    return {domainList: state.common['integrations_endpoints_integrations-list']}
}

export default connect(mapStateToProps, {post})(DomainList);
